import React, { Component } from 'react'
import ProductItem from './ProductItem';

class ProductList extends Component {

    showProduct() {
        return this.props.products && this.props.products.map(product => {
            const { productId } = product
            // return <ProductItem productName={productName} unitPrice={unitPrice} />
            return <ProductItem key={productId} product={product} onAddOrder={this.props.onAddOrder} onDelProduct={this.props.onDelProduct}/>
        })
    }

    render() {
        return (
            <div className="row">
                {this.showProduct()}
            </div>
        )
    }
}

export default ProductList